.wrapper {
  margin: 0 auto;
  max-width: 100vh;
  padding: 5% 8%;
  overflow: hidden;
}
html {
  --fontSize: 36pt;
  --colorForm: royalblue;
  --colorTrue: springgreen;
  --color-FalseReset: crimson;
  --borderRadius: 4px;
}
.form {
  display: flex;
  flex-direction: column;
  padding: 2% 4%;
  background-color: var(--colorForm);
  border-radius: var(--borderRadius);
  box-shadow: 0 0 0px 2px white, 0 0 0px 4px var(--colorForm),
    0 0 3px 3px black;
}
.form button {
  padding: 2% 4%;
  margin: 5% 0 0 0;
  font-size: var(--fontSize);
  font-weight: 700;
  border: 4px solid transparent;
  border-radius: var(--borderRadius);
  color: white;
  background-color: black;
  text-align: center;
  box-shadow: 0 0 2px black;
  text-transform: uppercase;
  cursor: pointer;
}
.form button:active {
  border: 4px inset gray;
  color: gray;
}
.form button.rst {
  padding: 0;
  margin: 4% 0;
  height: 6vh;
  min-height: 20px;
  font-size: calc(var(--fontSize) / 2);
  background-color: var(--color-FalseReset);
  letter-spacing: 1.5px;
}
.form input {
  height: calc(var(--fontSize) * 1.4);
  font-size: calc(var(--fontSize) / 1.2);
  padding: 10px;
  border-radius: var(--borderRadius);
}
.form label {
  font-size: calc(var(--fontSize) / 3);
  font-weight: 700;
  padding: 3% 0;
  color: white;
  text-shadow: 0 0 5px black;
}
.outTxt {
  margin: 6% 0;
  padding: 1% 2%;
  border-radius: var(--borderRadius);
  display: none;
}
.green {
  background-color: var(--colorTrue);
  box-shadow: 0 0 0px 2px white, 0 0 1px 2px var(--colorTrue),
    0 0 3px 2px #666;
}
.red {
  color: white;
  background-color: var(--color-FalseReset);
  box-shadow: 0 0 0px 2px white, 0 0 1px 2px var(--color-FalseReset),
    0 0 3px 2px #111;
}
.outTxt h1 {
  text-transform: uppercase;
  text-align: center;
  font-size: calc(var(--fontSize) / 1.2);
}
.active {
  display: block;
}
@media screen {
  .form button:hover {
    box-shadow: 0 0 3px white, 0 0 6px var(--colorForm);
    text-shadow: 0 0 3px white, 0 0 6px var(--colorForm);
  }
}
@media screen and (min-width: 1024px) {
  html {
    --fontSize: 40pt !important;
  }
  .wrapper {
    padding: 2%;
    max-width: 640px;
    margin: 0 auto;
  }
}
@media screen and (orientation: landscape) {
  html {
    --fontSize: 18pt;
    min-height: 320px;
  }
  .wrapper {
    min-width: 320px;
    padding: 2% 8%;
  }
  .form label {
    font-size: calc(var(--fontSize) / 2);
  }
  .form button.rst {
    height: 7vh;
  }
}
